// *** begin screen *** //
.screen {
   display: flex;
   height: calc(100vh - 140px);
   padding-top: 140px;
   color: #fff;
   background: $color2;
   &__container {
      display: flex;
      flex-direction: column;
      justify-content: center; }
   &__inner {
      display: flex;
      align-items: center;
      max-width: 1005px;
      padding-left: 30px; }
   &__triangle {}
   &__titles-wrap {
      position: relative;
      flex: 0 1 390px;
      font-size: 60px;
      text-transform: uppercase;
      &::after {
         position: absolute;
         top: 47px;
         right: 0;
         width: 73px;
         height: 6px;
         background-color: $color5;
         content: ''; } }
   &__title {
      @include f('gp700', 150, 150); }
   &__sub-title {
      letter-spacing: 12px; }
   &__descr {
      flex: 0 1 540px;
      &-text {
         margin-bottom: 20px;
         font-size: 26px;
         line-height: 40px;
         &-mark {
            @include f('gp500');
            &_color-1 {
               color: #fe77af; }
            &_color-2 {
               color: #ffbd6a; } } }
      &-btn {} } }
// end screen //

// *** begin about *** //
.about {
   padding: 50px 0;
   &__container {
      display: flex;
      align-items: center; }
   &__text {
      flex: 0 1 percentage(680 / 1140); }
   &__title {
      margin-bottom: 30px; }
   &__descr {
      p {
         &:not(:last-child) {
            margin-bottom: 20px; } } }
   &__img {
      flex: 0 1 percentage(440 / 1140);
      @include ratio(440, 482);
      @include cover; } }
// end about //

// *** begin services *** //
.services {
   padding: 50px 0;
   &__title {
      margin-bottom: 50px; }
   &__row {
      margin: 0 -17px; }
   &__item {
      $parent: &;
      position: relative;
      flex: 0 1 calc(33.33333% - 34px);
      max-width: 360px;
      margin: 0 17px;
      padding: 20px;
      &::after {
         position: absolute;
         top: 20px;
         left: 20px;
         right: 20px;
         bottom: 20px;
         border-radius: 4px;
         border: 1px solid $color3;
         opacity: 0.1;
         pointer-events: none;
         content: ''; }
      &:nth-child(3) ~ & {
         margin-top: 34px; }
      &-corner {
         position: absolute;
         width: 20px;
         height: 20px;
         border-style: solid;
         border-color: $color3;
         transition: .3s ease-in-out;
         #{$parent}:hover & {
            opacity: 0; }
         &_top {
            top: 20px;
            left: 20px;
            border-left-width: 1px;
            border-top-width: 1px;
            #{$parent}:hover & {
               top: 0;
               left: 0; } }
         &_bot {
            bottom: 20px;
            right: 20px;
            border-bottom-width: 1px;
            border-right-width: 1px;
            #{$parent}:hover & {
               bottom: 0;
               right: 0; } } }
      &-inner {
         display: flex;
         align-items: center;
         padding: 15px; }
      &-icon {
         z-index: 2;
         &-wrap {
            position: relative;
            flex: 0 0 112px;
            &::after, &::before {
               position: absolute;
               z-index: 1;
               width: 59px;
               height: 59px;
               background-color: #c2c2c2;
               opacity: 0.1;
               content: '';
               border-radius: 50%; }
            &::after {}

            &::before {} } }
      &-text {
         padding-left: 15px; }
      &-title {
         margin-bottom: 5px;
         @include f('gp700', 22);
         color: $color4; }
      &-price {
         span {
            @include f('gp700', 18);
            color: $color2; } }
      &-list {
         li {} } } }
// end services //

// *** begin how-to *** //
.how-to {
   padding: 50px 0;
   &__title {
      margin-bottom: 30px; }
   &__row {
      margin: 0 -17px;
      counter-reset: howToItem; }
   &__item {
      position: relative;
      flex: 0 1 calc(25% - 34px);
      margin: 0 17px;
      padding: 20px;
      text-align: center;
      counter-increment: howToItem;
      overflow: hidden;
      &::before {
         position: absolute;
         left: 0;
         top: 50%;
         transform: translateY(-50%);
         text-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
         color: $color2;
         @include f('gp900');
         font-size: 34px;
         line-height: 18px;
         content: counter(howToItem, decimal-leading-zero); }
      &::after {
         position: absolute;
         top: 0;
         left: 0;
         opacity: 0.04;
         color: $color2;
         @include f('gp900');
         font-size: 305px;
         line-height: 200px;
         content: counter(howToItem, decimal-leading-zero); }
      &-icon {
         &-wrap {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 90px;
            height: 90px;
            margin-bottom: 20px;
            border-radius: 50%;
            &_color-1 {
               background-color: $color5; }
            &_color-2 {
               background-color: #f1ae2f; }
            &_color-3 {
               background-color: #fe77af; } } } } }

// end how-to //

// *** begin train *** //
.train {
   padding: 50px 0;
   &__container {
      display: flex;
      align-items: center;
      justify-content: space-between; }
   &__img {
      flex: 0 1 percentage(374 / 1140);
      @include ratio(374, 370);
      @include cover; }
   &__text {
      flex: 0 1 percentage(670 / 1140);
      p {
         font-size: 18px;
         line-height: 28px;
         span {
            @include f('gp700');
            color: $color5; } }
      &-list {
         li::before {
            background-color: #fe77af; } } }
   &__title {
      margin-bottom: 30px; } }
// end train //

// *** begin pass *** //
.pass {
   &__title {
      margin-bottom: 30px; }
   &__text {
      margin-bottom: 30px;
      p {
         &:not(:last-child) {
            margin-bottom: 15px; } } }
   &__videos {
      max-width: 1000px;
      margin: 0 auto 40px;
      &-row {
         margin: 0 -20px; }
      &-col {
         position: relative;
         flex: 0 1 50%;
         padding: 0 40px;
         &::after, &::before {
            position: absolute;
            top: 50%;
            margin-top: -58px;
            width: 2px;
            height: 116px;
            background-color: $color5;
            opacity: 0.2;
            content: ''; }
         &:nth-child(2) ~ & {
            padding: 40px 40px 0; }
         &:nth-child(1n) {
            &::before {
               left: 0; }
            &::after {
               left: 100%;
               margin-left: -1px; } }
         &:nth-child(2n) {
            &::before {
               display: none; }
            &::after {
               right: 0; } } } }
   &__actions {
      text-align: center; } }
// end pass //

// *** begin testimonials *** //
.testimonials {
   &__container {
      max-width: 1190px;
 }      //max-width: 1150px
   &__title {
      margin-bottom: 30px; }
   &__slider {
      .slick-list {
         padding: 55px 20px 0;
         margin: 0 -25px; }
      .slider-btn {
         &_prev {
            left: -70px; }
         &_next {
            right: -70px; } } }
   &__item {
      position: relative;
      margin: 0 25px;
      opacity: 0;
      @include gpu;
      &.slick-active {
         opacity: 1; }
      &-inner {
         padding: 15px 18px;
         border-radius: 4px;
         background: #fff; }
      &-date {
         position: absolute;
         top: -45px;
         right: -17px;
         z-index: -1;
         width: 90px;
         height: 90px;
         padding: 15px 0 0;
         border-radius: 50%;
         box-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
         color: #fff;
         text-align: center;
         @include f('gp500', 14, 14);
         &_color-1 {
            background-color: $color5; }
         &_color-2 {
            background-color: #f1ae2f; }
         &_color-3 {
            background-color: #fe77af; } }
      &-title {
         margin-bottom: 15px;
         @include f('gp700', 18, 26);
         &_color-1 svg {
            fill: $color5; }
         &_color-2 svg {
            fill: #f1ae2f; }
         &_color-3 svg {
            fill: #fe77af; }
         svg {
            margin-right: 10px; } }
      p {
         font-size: 16px;
         line-height: 26px; } } }
// end testimonials //
