.slider {
   display: none;
   &.slick-initialized {
      display: block; } }

.slider-btn {
   position: absolute;
   top: 50%;
   display: flex;
   align-items: center;
   justify-content: center;
   width: 35px;
   height: 35px;
   margin-top: -17px;
   box-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
   background-color: $color6;
   border-radius: 50%;
   opacity: 0.5;
   transition: .3s ease-in-out;
   &:hover {
      opacity: 1; }
   &.slick-disabled {
      pointer-events: none;
      opacity: .3; }
   &_prev {
      left: 0;
      svg {
         left: -1px; } }
   &_next {
      right: 0;
      svg {
         right: -1px; } }
   svg {
      position: relative;
      fill: $white; } }
.slider-dots {}
